[id^="hornetcomb-header-"] {
    background: var(--brzdk) url(../svg/comb-back.svg);
    background-repeat: repeat;
    //    background-size: 35%;
    background-blend-mode: color-burn;
    h2 {
        color: var(--white);
        text-align: center;
        padding-bottom: 1rem;
    }
    .hexwrapper {
        margin: auto;
        @media screen and (min-width: 800px) {
            padding: 1rem 0;
        }
        .hex-row {
            // display: flex;
            ul {
                display: grid;
                margin: auto;
                width: calc(10rem * 1);
                gap: 1.5rem;
                margin: 0 auto;
                list-style: none;
                li {
                    position: relative;
                    display: flex;
                    &.nav-item {
                        border-bottom: 0;
                    }
                    -webkit-clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
                    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
                    background: var(--wgolt);
                    min-height: 8rem;
                    align-items: center;
                    a {
                        color: var(--ssgr);
                        hyphens: none;
                        padding: 2rem;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        text-align: center;
                        text-decoration: none;
                        width: 100%;
                        height: 100%;
                        font-weight: 600;
                        text-shadow: var(--gvgr) 1px 2px 0;
                        font-size: 1.1rem;
                        -webkit-clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
                        clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
                        text-shadow: unset;
                        &:hover,
                        &:focus {
                            color: var(--wgoDk);
                            text-decoration: underline;
                            text-shadow: unset;
                            background: var(--white);
                        }
                        @media (min-width: 500px) {
                            justify-content: center;
                            text-align: center;
                        }
                        @media (min-width: 800px) {
                            padding: 1rem;
                        }
                    }
                }
                @media screen and (max-width: 400px) {
                    grid-template-columns: repeat(1, 1fr);
                    grid-template-rows: repeat(3, 1fr);
                    &.hex-4,
                    &.hex-5,
                    &.hex-6 {
                        transform: translateX(-15%);
                    }
                }
                @media screen and (min-width: 401px) {
                    &.hex-3 {
                        // width: calc(10rem * 3);
                        width: unset;
                        max-width: calc(10rem * 3);
                        grid-template-columns: repeat(4, 1fr);
                        grid-template-rows: repeat(4, 1fr);
                        transform: translateX(0);
                        gap: 1rem;
                        li {
                            grid-column-end: span 2;
                            grid-row-end: span 2;
                            &:nth-child(1) {
                                grid-row-start: 1;
                                grid-column-start: 1;
                            }
                            &:nth-child(2) {
                                grid-row-start: 1;
                                grid-column-start: 3;
                            }
                            &:nth-child(3) {
                                grid-column-start: 2;
                            }
                        }
                    }
                    &.hex-4 {
                        grid-template-columns: repeat(5, 1fr);
                        grid-template-rows: repeat(5, 1fr);
                        width: unset;
                        max-width: calc(10rem * 4);
                        li {
                            grid-column-end: span 3;
                            grid-row-end: span 2;
                            &:nth-child(1) {
                                grid-column-start: 1;
                                grid-row-start: 1;
                            }
                            &:nth-child(2) {
                                grid-column-start: 3;
                                grid-row-start: 2;
                            }
                            &:nth-child(3) {
                                grid-column-start: 1;
                                grid-row-start: 3;
                            }
                            &:nth-child(4) {
                                grid-column-start: 3;
                                grid-row-start: 4;
                            }
                        }
                    }
                    &.hex-5 {
                        grid-template-columns: repeat(7, 1fr);
                        grid-template-rows: repeat(4, 1fr);
                        width: unset;
                        max-width: calc(10rem * 3);
                        li {
                            grid-column-end: span 3;
                            grid-row-end: span 2;
                            &:nth-child(1) {
                                grid-column-start: 1;
                                grid-row-start: 1;
                            }
                            &:nth-child(2) {
                                grid-column-start: 5;
                                grid-row-start: 1;
                            }
                            &:nth-child(3) {
                                grid-column-start: 3;
                                grid-row-start: 2;
                            }
                            &:nth-child(4) {
                                grid-column-start: 1;
                                grid-row-start: 3;
                            }
                            &:nth-child(5) {
                                grid-column-start: 5;
                                grid-row-start: 3;
                            }
                        }
                    }
                    &.hex-6 {
                        grid-template-columns: repeat(5, 1fr);
                        grid-template-rows: repeat(4, 1fr);
                        width: unset;
                        max-width: calc(10rem * 3);
                        li {
                            grid-column-end: span 3;
                            grid-row-end: span 2;
                            &:nth-child(1) {
                                grid-column-start: 1;
                                grid-row-start: 1;
                            }
                            &:nth-child(2) {
                                grid-column-start: 3;
                                grid-row-start: 2;
                            }
                            &:nth-child(3) {
                                grid-column-start: 1;
                                grid-row-start: 3;
                            }
                            &:nth-child(4) {
                                grid-column-start: 3;
                                grid-row-start: 4;
                            }
                            &:nth-child(5) {
                                grid-column-start: 1;
                                grid-row-start: 5;
                            }
                            &:nth-child(6) {
                                grid-column-start: 3;
                                grid-row-start: 6;
                            }
                        }
                    }
                }
                @media screen and (min-width: 601px) {
                    &.hex-3 {
                        grid-template-columns: repeat(12, 1fr);
                        grid-template-rows: repeat(3, 1fr);
                        max-width: calc(12rem * 3);
                        li {
                            grid-column-end: span 4;
                            &:first-child {
                                grid-column-start: 2;
                            }
                            &:nth-child(2) {
                                grid-row-start: 2;
                                grid-column-start: 5;
                            }
                            &:nth-child(3) {
                                grid-row-start: 1;
                                grid-column-start: 8;
                            }
                        }
                    }
                    &.hex-4 {
                        grid-template-columns: repeat(5, 1fr);
                        grid-template-rows: repeat(4, 1fr);
                        //width: calc(10rem * 4);
                        // gap: 5%;
                        li {
                            grid-column-end: span 2;
                            grid-row-end: span 2;
                            &:nth-child(1) {
                                grid-column-start: 1;
                                grid-row-start: 1;
                            }
                            &:nth-child(2) {
                                grid-column-start: 3;
                                grid-row-start: 1;
                            }
                            &:nth-child(3) {
                                grid-column-start: 2;
                                grid-row-start: 3;
                            }
                            &:nth-child(4) {
                                grid-column-start: 4;
                                grid-row-start: 3;
                            }
                        }
                    }
                    &.hex-6 {
                        grid-template-columns: repeat(7, 1fr);
                        grid-template-rows: repeat(5, 1fr);
                        max-width: unset;
                        li {
                            grid-column-end: span 3;
                            grid-row-end: span 2;
                            &:nth-child(1) {
                                grid-column-start: 1;
                                grid-row-start: 2;
                            }
                            &:nth-child(2) {
                                grid-column-start: 3;
                                grid-row-start: 1;
                            }
                            &:nth-child(3) {
                                grid-column-start: 5;
                                grid-row-start: 2;
                            }
                            &:nth-child(4) {
                                grid-column-start: 3;
                                grid-row-start: 3;
                            }
                            &:nth-child(5) {
                                grid-column-start: 1;
                                grid-row-start: 4;
                            }
                            &:nth-child(6) {
                                grid-column-start: 5;
                                grid-row-start: 4;
                            }
                        }
                    }
                }
                @media screen and (min-width: 901px) {
                    &.hex-4 {
                        grid-template-columns: repeat(8, 1fr);
                        grid-template-rows: repeat(3, 1fr);
                        //width: calc(10rem * 4);
                        gap: 0;
                        li {
                            grid-column-end: span 2;
                            grid-row-end: span 2;
                            &:nth-child(1) {
                                grid-column-start: 1;
                                grid-row-start: 1;
                            }
                            &:nth-child(2) {
                                grid-column-start: 5;
                                grid-row-start: 1;
                            }
                            &:nth-child(3) {
                                grid-column-start: 3;
                                grid-row-start: 2;
                            }
                            &:nth-child(4) {
                                grid-column-start: 7;
                                grid-row-start: 2;
                            }
                        }
                    }
                    &.hex-5 {
                        grid-template-columns: repeat(11, 1fr);
                        grid-template-rows: repeat(3, 1fr);
                        max-width: calc(10rem * 5);
                        // gap: 5%;
                        li {
                            grid-column-end: span 3;
                            grid-row-end: span 2;
                            &:nth-child(1) {
                                grid-column-start: 1;
                                grid-row-start: 1;
                            }
                            &:nth-child(2) {
                                grid-column-start: 5;
                                grid-row-start: 1;
                            }
                            &:nth-child(3) {
                                grid-column-start: 3;
                                grid-row-start: 2;
                            }
                            &:nth-child(4) {
                                grid-column-start: 7;
                                grid-row-start: 2;
                            }
                            &:nth-child(5) {
                                grid-column-start: 9;
                                grid-row-start: 1;
                            }
                        }
                    }
                    &.hex-6 {
                        grid-template-columns: repeat(26, 1fr);
                        grid-template-rows: repeat(5, 1fr);
                        max-width: calc(10rem * 5);
                        gap: .5rem;
                        li {
                            grid-column-end: span 6;
                            grid-row-end: span 3;
                            &:first-child {
                                grid-column-start: 1;
                                grid-row-start: 1;
                            }
                            &:nth-child(2) {
                                grid-column-start: 5;
                                grid-row-start: 3;
                            }
                            &:nth-child(3) {
                                grid-column-start: 9;
                                grid-row-start: 1;
                            }
                            &:nth-child(4) {
                                grid-column-start: 13;
                                grid-row-start: 3;
                            }
                            &:nth-child(5) {
                                grid-column-start: 17;
                                grid-row-start: 1;
                            }
                            &:nth-child(6) {
                                grid-column-start: 21;
                                grid-row-start: 3;
                            }
                        }
                    }
                }
            }
        }
    }
}